export default {
  props: ["miniCart"],

  computed: {
    products() {
      return this.$store.getters.getCart;
    },
    total() {
      let total = 0;
      for (let i in this.$store.getters.getCart) {
        // console.log(this.$store.getters.getCart[i])
        let item =
          this.$store.getters.getCart[i].state.discounted_price *
          this.$store.getters.getCart[i].count;
        // console.log(item)
        total += item;
      }
      // console.log('total '+ total)
      return total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
};
